/**
 * ÊñπÂùóÁÆ°ÁêÜÂô®
 * Ë¥üË¥£ÁÆ°ÁêÜÂΩ©Ëâ≤ÊñπÂùóÁöÑÂàõÂª∫„ÄÅÊòæÁ§∫ÂíåÁ±ªÂûãÁÆ°ÁêÜ
 */

import { Node, SpriteAtlas, Sprite, SpriteFrame, instantiate, UITransform, Color, _decorator, Texture2D, ImageAsset, Rect } from 'cc';
const { ccclass, property } = _decorator;

export class BlockManager {
    private blockAtlas: SpriteAtlas = null!;
    private blockTypeCount: number = 5;
    
    // È¢ÑÂÆö‰πâÁöÑÈ¢úËâ≤ÊñπÊ°àÔºàÂ¶ÇÊûúÊ≤°ÊúâÂõæÈõÜÁöÑËØùÔºâ
    private blockColors: Color[] = [
        new Color(255, 100, 100, 255),  // Á∫¢Ëâ≤
        new Color(100, 255, 100, 255),  // ÁªøËâ≤
        new Color(100, 100, 255, 255),  // ËìùËâ≤
        new Color(255, 255, 100, 255),  // ÈªÑËâ≤
        new Color(255, 100, 255, 255),  // Á¥´Ëâ≤
        new Color(100, 255, 255, 255),  // ÈùíËâ≤
        new Color(255, 150, 100, 255),  // Ê©ôËâ≤
        new Color(150, 150, 150, 255),  // ÁÅ∞Ëâ≤
    ];
    
    /**
     * ÂàùÂßãÂåñÊñπÂùóÁÆ°ÁêÜÂô®
     */
    init(blockAtlas: SpriteAtlas | null, blockTypeCount: number) {
        this.blockAtlas = blockAtlas;
        this.blockTypeCount = Math.min(blockTypeCount, this.blockColors.length);
        
        console.log(`‚úÖ ÊñπÂùóÁÆ°ÁêÜÂô®ÂàùÂßãÂåñÂÆåÊàêÔºåÊñπÂùóÁ±ªÂûãÊï∞: ${this.blockTypeCount}`);
        
        if (!this.blockAtlas) {
            console.log('‚ö†Ô∏è Êú™ËÆæÁΩÆÊñπÂùóÂõæÈõÜÔºåÂ∞Ü‰ΩøÁî®Á∫ØËâ≤ÊñπÂùó');
        }
    }
    
    /**
     * ÂàõÂª∫ÊåáÂÆöÁ±ªÂûãÁöÑÊñπÂùóËäÇÁÇπ
     */
    createBlockNode(blockType: number, blockSize: number = 60): Node | null {
        if (blockType < 0 || blockType >= this.blockTypeCount) {
            console.error(`‚ùå Êó†ÊïàÁöÑÊñπÂùóÁ±ªÂûã: ${blockType}`);
            return null;
        }
        
        // ÂàõÂª∫Êñ∞ËäÇÁÇπ
        const blockNode = new Node(`Block_Type_${blockType}`);
        
        // Ê∑ªÂä†UITransformÁªÑ‰ª∂
        const transform = blockNode.addComponent(UITransform);
        transform.setContentSize(blockSize, blockSize);  // ‰ΩøÁî®Âä®ÊÄÅÊñπÂùóÂ§ßÂ∞è
        
        // Ê∑ªÂä†SpriteÁªÑ‰ª∂
        const sprite = blockNode.addComponent(Sprite);
        
        // ËÆæÁΩÆÊñπÂùóÂ§ñËßÇ
        this.setupBlockAppearance(sprite, blockType, blockSize);
        
        return blockNode;
    }
    
    /**
     * ËÆæÁΩÆÊñπÂùóÂ§ñËßÇ
     */
    private setupBlockAppearance(sprite: Sprite, blockType: number, blockSize: number) {
        console.log(`üé® ÂºÄÂßãËÆæÁΩÆÊñπÂùóÂ§ñËßÇ: Type ${blockType}, Size ${blockSize}px`);
        
        try {
            if (this.blockAtlas) {
                // ‰ΩøÁî®ÂõæÈõÜ‰∏≠ÁöÑsprite frame
                const spriteFrameName = `block_${blockType}`;
                const spriteFrame = this.blockAtlas.getSpriteFrame(spriteFrameName);
                
                if (spriteFrame) {
                    sprite.spriteFrame = spriteFrame;
                    console.log(`üé® ‰ΩøÁî®ÂõæÈõÜsprite: ${spriteFrameName}`);
                } else {
                    console.warn(`‚ö†Ô∏è ÂõæÈõÜ‰∏≠Êú™ÊâæÂà∞ ${spriteFrameName}Ôºå‰ΩøÁî®Á∫ØËâ≤ÊñπÂùó`);
                    this.setupColorBlock(sprite, blockType, blockSize);
                }
            } else {
                // ‰ΩøÁî®Á∫ØËâ≤ÊñπÂùó
                console.log(`üé® ‰ΩøÁî®Á∫ØËâ≤ÊñπÂùóÊ®°Âºè`);
                this.setupColorBlock(sprite, blockType, blockSize);
            }
            console.log(`‚úÖ ÊñπÂùóÂ§ñËßÇËÆæÁΩÆÂÆåÊàê`);
        } catch (error) {
            console.error(`‚ùå ËÆæÁΩÆÊñπÂùóÂ§ñËßÇÊó∂Âá∫Èîô:`, error);
            // ÁÆÄÂçïÁöÑÂõûÈÄÄÊñπÊ°àÔºöÂè™ËÆæÁΩÆÈ¢úËâ≤Ôºå‰∏ç‰ΩøÁî®SpriteFrame
            try {
                sprite.color = this.blockColors[blockType] || this.blockColors[0];
                console.log(`üîß ‰ΩøÁî®ÁÆÄÂçïÈ¢úËâ≤ÂõûÈÄÄÊñπÊ°à: ${sprite.color.toHEX()}`);
            } catch (fallbackError) {
                console.error(`‚ùå ÂõûÈÄÄÊñπÊ°à‰πüÂ§±Ë¥•:`, fallbackError);
            }
        }
    }
    
    /**
     * ËÆæÁΩÆÁ∫ØËâ≤ÊñπÂùó
     */
    private setupColorBlock(sprite: Sprite, blockType: number, blockSize: number) {
        console.log(`üé® ÂºÄÂßãËÆæÁΩÆÁ∫ØËâ≤ÊñπÂùó: Type ${blockType}, Size ${blockSize}px`);
        
        try {
            // ÂàõÂª∫‰∏éÊñπÂùóÂ∞∫ÂØ∏ÂåπÈÖçÁöÑÁ∫πÁêÜ
            console.log(`üîß ÂàõÂª∫SpriteFrame...`);
            this.createDefaultSpriteFrame(sprite, blockSize);
            
            // ËÆæÁΩÆÈ¢úËâ≤
            if (blockType < this.blockColors.length) {
                sprite.color = this.blockColors[blockType];
                console.log(`üé® ËÆæÁΩÆÊñπÂùóÈ¢úËâ≤: Type ${blockType} -> ${this.blockColors[blockType].toHEX()}`);
            } else {
                console.warn(`‚ö†Ô∏è ÊñπÂùóÁ±ªÂûãË∂ÖÂá∫ËåÉÂõ¥: ${blockType}Ôºå‰ΩøÁî®ÈªòËÆ§È¢úËâ≤`);
                sprite.color = this.blockColors[0];
            }
            console.log(`‚úÖ Á∫ØËâ≤ÊñπÂùóËÆæÁΩÆÂÆåÊàê`);
        } catch (error) {
            console.error(`‚ùå ËÆæÁΩÆÁ∫ØËâ≤ÊñπÂùóÊó∂Âá∫Èîô:`, error);
            console.log(`üîß Â∞ùËØïÊúÄÁÆÄÂçïÁöÑÈ¢úËâ≤ËÆæÁΩÆ...`);
            sprite.color = this.blockColors[0]; // Á∫¢Ëâ≤‰Ωú‰∏∫ÂõûÈÄÄ
        }
    }
    
    /**
     * ÂàõÂª∫‰∏éUITransformÂ∞∫ÂØ∏ÂåπÈÖçÁöÑSpriteFrame
     */
    private createDefaultSpriteFrame(sprite: Sprite, size: number = 40) {
        // ‰ΩøÁî®‰∏éUITransformÁõ∏ÂêåÁöÑÂ∞∫ÂØ∏ÔºåÂ∏¶1pxÁôΩËæπÊ°Ü
        const canvas = document.createElement('canvas');
        canvas.width = size;
        canvas.height = size;
        const ctx = canvas.getContext('2d')!;
        
        // Â°´ÂÖÖÁôΩËâ≤ËÉåÊôØÔºàËæπÊ°ÜÔºâ
        ctx.fillStyle = 'white';
        ctx.fillRect(0, 0, size, size);
        
        // Â°´ÂÖÖÂÜÖÈÉ®Âå∫ÂüüÔºàÁïôÂá∫1pxËæπÊ°ÜÔºâ
        ctx.fillStyle = 'gray'; // ÁÅ∞Ëâ≤ÂÜÖÈÉ®ÔºåÈ¢úËâ≤‰ºöË¢´Sprite.colorË¶ÜÁõñ
        ctx.fillRect(1, 1, size-2, size-2);
        
        // ÂàõÂª∫Á∫πÁêÜ
        const texture = new Texture2D();
        const imageAsset = new ImageAsset();
        imageAsset._nativeAsset = canvas;
        texture.image = imageAsset;
        
        // ÂàõÂª∫SpriteFrame
        const spriteFrame = new SpriteFrame();
        spriteFrame.texture = texture;
        spriteFrame.rect = new Rect(0, 0, size, size);
        
        sprite.spriteFrame = spriteFrame;
        console.log(`‚úÖ ÂàõÂª∫Â∞∫ÂØ∏ÂåπÈÖçÁöÑSpriteFrameÊàêÂäü: ${size}√ó${size}px`);
    }
    
    /**
     * ÂàõÂª∫ÊñπÂùóÁöÑÂ§çÂà∂ÂìÅÔºàÁî®‰∫éÂä®ÁîªÁ≠âÔºâ
     */
    cloneBlockNode(originalNode: Node): Node | null {
        if (!originalNode) return null;
        
        const clonedNode = instantiate(originalNode);
        clonedNode.name = originalNode.name + '_Clone';
        
        return clonedNode;
    }
    
    /**
     * Ëé∑ÂèñÊñπÂùóÁ±ªÂûãÊï∞Èáè
     */
    getBlockTypeCount(): number {
        return this.blockTypeCount;
    }
    
    /**
     * Ëé∑ÂèñÊñπÂùóÈ¢úËâ≤
     */
    getBlockColor(blockType: number): Color {
        if (blockType >= 0 && blockType < this.blockColors.length) {
            return this.blockColors[blockType];
        }
        return Color.WHITE;
    }
    
    /**
     * ËÆæÁΩÆÊñπÂùóÁöÑÈÄèÊòéÂ∫¶
     */
    setBlockOpacity(blockNode: Node, opacity: number) {
        const sprite = blockNode.getComponent(Sprite);
        if (sprite) {
            const color = sprite.color.clone();
            color.a = opacity;
            sprite.color = color;
        }
    }
    
    /**
     * ËÆæÁΩÆÊñπÂùóÁöÑÈ´ò‰∫ÆÊïàÊûú
     */
    setBlockHighlight(blockNode: Node, highlight: boolean) {
        const sprite = blockNode.getComponent(Sprite);
        if (sprite) {
            if (highlight) {
                // È´ò‰∫ÆÔºöÂ¢ûÂä†‰∫ÆÂ∫¶
                const color = sprite.color.clone();
                color.r = Math.min(255, color.r * 1.2);
                color.g = Math.min(255, color.g * 1.2);
                color.b = Math.min(255, color.b * 1.2);
                sprite.color = color;
            } else {
                // ÊÅ¢Â§çÂéüËâ≤ÔºöÈúÄË¶ÅÁü•ÈÅìÂéüÂßãÁ±ªÂûã
                // ËøôÈáåÁÆÄÂåñÂ§ÑÁêÜÔºåÂÆûÈôÖÈ°πÁõÆ‰∏≠ÂèØËÉΩÈúÄË¶ÅÂ≠òÂÇ®ÂéüÂßãÈ¢úËâ≤
                const blockName = blockNode.name;
                const typeMatch = blockName.match(/Block_Type_(\d+)/);
                if (typeMatch) {
                    const blockType = parseInt(typeMatch[1]);
                    this.setupBlockAppearance(sprite, blockType);
                }
            }
        }
    }
    
    /**
     * Ëé∑ÂèñÊñπÂùóÁ±ªÂûãÔºà‰ªéËäÇÁÇπÂêçÁß∞Ëß£ÊûêÔºâ
     */
    getBlockTypeFromNode(blockNode: Node): number {
        const blockName = blockNode.name;
        const typeMatch = blockName.match(/Block_Type_(\d+)/);
        if (typeMatch) {
            return parseInt(typeMatch[1]);
        }
        return -1;
    }
    
    /**
     * È™åËØÅÊñπÂùóÁ±ªÂûãÊòØÂê¶ÊúâÊïà
     */
    isValidBlockType(blockType: number): boolean {
        return blockType >= 0 && blockType < this.blockTypeCount;
    }
    
    /**
     * Ëé∑ÂèñÈöèÊú∫ÊñπÂùóÁ±ªÂûã
     */
    getRandomBlockType(): number {
        return Math.floor(Math.random() * this.blockTypeCount);
    }
    
    /**
     * Ë∞ÉËØïÔºöÊâìÂç∞ÊñπÂùó‰ø°ÊÅØ
     */
    debugPrintBlockInfo(blockNode: Node) {
        const blockType = this.getBlockTypeFromNode(blockNode);
        const sprite = blockNode.getComponent(Sprite);
        const color = sprite ? sprite.color : null;
        
        console.log(`üîç ÊñπÂùó‰ø°ÊÅØ: ${blockNode.name}, Type: ${blockType}, Color: ${color?.toHEX()}`);
    }
}
